.features {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  @include md {
    margin-right: -10px;
    margin-left: -10px; } }

.feature {
  text-align: center;
  $this: &;
  &__icon {
    @include cover;
    background-size: contain;
    background-position: 0% 50%;
    @include inline-flex(center, center);
    width: 170px;
    height: 170px;
    @include xs {
      width: 120px;
      height: 120px;
      .icon {
        font-size: 45px; } }
    .icon {
      fill: $white;
      font-size: 60px; } }
  &__wrap {
    padding-left: 15px;
    padding-right: 15px;
    width: 25%;
    @include md {
      padding-left: 10px;
      padding-right: 10px;
      width: 33.33333%; }
    @include sm {
      width: 50%; }
    &:nth-child(1) {}

    &:nth-child(2) {
      #{$this}__icon {
        background-position: 20px 0; } }
    &:nth-child(3) {
      #{$this}__icon {
        background-position: 0 100%; } }
    &:nth-child(4) {}
    &:nth-child(5) {
      #{$this}__icon {
        background-position: 20px 100%; } }
    &:nth-child(6) {}
    &:nth-child(7) {
      #{$this}__icon {
        background-position: 20px 0; } }
    &:nth-child(8) {
      #{$this}__icon {
        background-position: 5px 0; } } } }

.features-list {
  @include flex(flex-start, normal, row, wrap);
  margin-left: auto;
  margin-right: auto;
  max-width: 1250px;
  @include r(1250) {
    margin-left: -15px;
    margin-right: -15px; } }

.feature-item {
  background-color: $brown-lighten;
  border-radius: $radius;
  padding: 30px 20px;
  padding-left: 70px;
  height: 100%;
  color: $yellow-darken;
  font-weight: 700;
  font-size: $fz-sm;
  position: relative;
  @include md {
    padding-left: 60px; }
  &__wrap {
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;
    margin-bottom: 15px;
    @include sm {
      width: 100%; } }


  &__icon {
    background-color: $white;
    font-size: 0;
    border-radius: 50%;
    position: absolute;
    top: 30px;
    left: 20px;
    .icon {
      fill: $purple;
      font-size: 40px;
      @include md {
        font-size: 30px; } } } }
