.social {
  color: $gray-light;
  li {
    display: inline-block;
    @include notlast {
      margin-right: 20px; } }
  a {
    display: block;
    @include tr(color .3s);
    font-size: 21px;
    @include hover {
      color: $accent; } }
  .icon {
    vertical-align: middle; }
  .icon-youtube {
    font-size: 1.65em; }

  &--lg {
    color: $purple-lighten;
    a {
      font-size: 40px;
      @include sm {
        font-size: 30px; }
      @include hover {
        color: $white; } }
    li {
      margin-left: 15px;
      margin-right: 15px;
      @include sm;
      @include notlast {
        margin-left: 15px;
        margin-right: 15px;
        @include sm; } } } }

.social-block {
  position: relative;
  color: $purple-lighten;
  &__inner {
    min-width: 450px;
    margin-left: auto;
    margin-right: auto;
    @include flex(center, center);
    border-radius: $radius;
    padding-top: 45px;
    padding-bottom: 45px;
    padding-left: 10px;
    padding-right: 10px;
    border: 2px solid  $purple-lighten;
    @include lg {
      padding-top: 35px;
      padding-bottom: 35px;
      min-width: 400px; }
    @include xs {
      min-width: 0; } }
  &__title {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $purple-light;
    padding-left: 35px;
    padding-right: 35px;
    white-space: nowrap;
    @include lg {
      padding-left: 20px;
      padding-right: 20px; }
    @include xs {
      position: static;
      transform: translate(0, 0);
      margin-bottom: 10px;
      background-color: transparent;
      padding: 0; } } }

