.input {
  input {
    font-size: $fz-sm;
    height: 30px;
    border-bottom: 1px solid  $gray-light;
    @include tr(border-color .3s);
    width: 100%;
    color: $gray-light;
    &:focus {
      border-color: $accent; }
    @include placeholder {
      color: $gray-light; } } }

.textarea {
  textarea {
    font-size: $fz-sm;
    border: 1px solid  $gray-light;
    resize: none;
    padding: 15px;
    border-radius: $radius-sm;
    width: 100%;
    min-height: 100px;
    @include tr(border-color .3s);
    &:focus {
      border-color: $accent; }
    @include placeholder {
      color: $gray-light; } }

  &--input {
    font-size: 0;
    textarea {
      border: 0;
      border-bottom: 1px solid  $gray-light;
      min-height: 0;
      min-height: 30px;
      height: 30px;
      border-radius: 0;
      padding: 0;
      padding-top: 5px;
      overflow: hidden; } } }


.inputs {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -25px;
  margin-right: -25px;
  @include xl {
    margin-left: -15px;
    margin-right: -15px; }
  @include sm {
    margin-left: -10px;
    margin-right: -10px; }
  @include xs {
    margin-left: 0;
    margin-right: 0; }
  &__field {
    width: 33.33333%;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 30px;
    @include xl {
      padding-left: 15px;
      padding-right: 15px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      padding-left: 10px;
      padding-right: 10px; }
    @include xs {
      padding-left: 0;
      padding-right: 0;
      width: 100%; } } }
